import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 1,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

const list = document.querySelector(".todoList");

document.querySelector(".inputField > button").onclick = () => {
  newTodo();
};

document.querySelector(".inputField > input").onkeyup = (event) => {
  if (event.key === "Enter") {
    newTodo();
  }
};

function newTodo() {
  let inputText = document.querySelector(".inputField > input").value;
  let newTodo = {
    todoId: todos.length + 1,
    todoText: inputText,
    todoComplete: false
  };
  todos.push(newTodo);

  for (const item of todos) {
    let newLi = document.createElement("li");
    newLi.classList.add(`todo${item.todoId}`);
    let taskName = document.createTextNode(inputText);
    let span = document.createElement("span");
    let icon = document.createElement("i");
    icon.classList.add("fa-trash");

    span.appendChild(icon);

    newLi.appendChild(taskName);
    newLi.appendChild(span);

    list.appendChild(newLi);
  }
}
